.main {
  --columns-count: 1;

  display: grid;
  grid-template-columns: repeat(var(--columns-count), 200px);
  gap: 46px 48px;
  justify-content: center;
  padding: 50px 40px;
}

.card {
  grid-column: span 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 200px;
  border: 1px solid $grey-elements;
  border-radius: 5px;
  padding: 32px 16px 0;
  transition: all $hover-duration;

  &:hover {
    transform: scale(1.2);
  }

  &__image {
    width: 160px;
    height: 134px;
    margin-bottom: 40px;
  }

  &__title {
    align-self: flex-start;
    font-size: 12px;
    line-height: 18px;
    font-weight: 500;
    margin: 0 0 4px;

    &:hover {
      color: #34568b;
    }
  }

  &__code {
    font-size: 10px;
    line-height: 14px;
    color: $secondary-accent;
    margin: 0 0 16px;
    align-self: flex-start;
  }

  &__rating {
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;

    &-reviews {
      align-self: flex-end;
      font-size: 10px;
      line-height: 14px;
      margin: 0;
    }
  }

  &__price {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    margin: 0 0 16px;

    &-text {
      font-size: 12px;
      line-height: 18px;
      color: $secondary-accent;
      margin: 0;
    }

    &-number {
      font-size: 16px;
      line-height: 18px;
      font-weight: 700;
      margin: 0;
    }
  }

  &__button {
    width: 100%;
    height: 40px;
    border: 0;
    border-radius: 5px;
    background-color: $hover-link-color;
    color: $white-color;
    margin: 0 16px 16px;
    font-size: 14px;
    line-height: 16px;
    font-weight: 700;
    text-align: center;
    text-transform: uppercase;

    &:hover {
      border: 1px solid $hover-link-color;
      background-color: transparent;
      color: $hover-link-color;
    }
  }
}

@include on-phone {
  .main {
    --columns-count: 2;
  }
}

@include on-tablet {
  .main {
    --columns-count: 3;
  }
}

@include on-desktop {
  .main {
    --columns-count: 4;
  }
}
